@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    .utility-button {
        @apply bg-blue-500 text-white font-bold py-2 px-4 rounded border-[4px] border-black transition-all duration-500 hover:border-green-400 hover:bg-blue-900;
    }
    .msg-sent{
        @apply bg-blue-400 rounded font-extralight w-fit h-fit px-3 mx-9;
    }
    .msg-received{
        @apply bg-green-400 rounded font-extralight w-fit h-fit px-3 ;
    }
    
}